<template>
  <div id="product">
    <h1>產品介紹</h1>
    <div class="gallery">
      <div class="gallery__splide">
        <splide :options="splideOptions">
          <splide-slide
          v-for="(img, index) in current_product.images"
          :key="index">
            <img :src="img">
          </splide-slide>
        </splide>
      </div>
    </div>
    <div class="lightbox">
      <div class="lightbox__showbox">
        <img :src="lightbox_showbox" alt="">
      </div>
      <ul class="lightbox__list">
        <li class="lightbox__list__item"
        v-for="(image, index) in current_product.images"
        :key="index">
          <img alt=""
          :src="image"
          @click.prevent='lightbox_index = index'
          @mouseover='lightbox_showbox = image'>
        </li>
      </ul>
      <vue-gallery-slideshow
      :images="current_product.images"
      :index="lightbox_index"
      @close="lightbox_index = null"></vue-gallery-slideshow>
    </div>
    <div class="main">
      <div class="main__title">
        <span class="title">{{current_product.title}}</span>
        <span class="code">{{current_product.id}}</span>
      </div>
      <div class="main__content">
        <p class="content" v-html="current_product.content"></p>
      </div>
      <div class="main__info">
        <div class="main__info__price">NT$ {{current_product.price}}</div>
        <div class="main__info__quantity">
          <a href="#" class="quantityControl quantityControl--minus">
            <img src="../../assets/icon/qty_minus.png" alt="">
          </a>
          <input type="text" class="quantityControl--input">
          <a href="#" class="quantityControl quantityControl--plus">
            <img src="../../assets/icon/qty_plus.png" alt="">
          </a>
        </div>
        <div class="main__info__purchase">
          <a href="#" class="addToCart">加入購物車</a>
          <a href="#" class="buyDirectly">直接購買</a>
          <p class="remark">台灣本島地區全站商品免運配送</p>
        </div>
        <div class="main__info__favorite">
          <label class="label">
            <input type="checkbox" class="checkbox">
            <img class="not-favorite" src="../../assets/icon/gold_heart_empty_icon.png" alt="">
            <img class="favorite" src="../../assets/icon/gold_heart_icon.png" alt="">
            加入喜好項目
          </label>
        </div>
        <div class="main__info__description">
          <ul class="list">
            <li class="list__item"
            v-for="(description, index) in current_product.description"
            :key="index"
            :class="{active: description.active}">
              <a href="#"
              @click.prevent='change_description(index)'>
                {{description.title}}
              </a>
            </li>
          </ul>
          <div class="descriptionText"
          v-html="current_description">
          </div>
        </div>
      </div>
    </div>
    <div class="relatedProduct">
      <p class="relatedProduct__title">
        <span>相關商品</span>
      </p>
      <ul class="relatedProduct__list">
        <li class="relatedProduct__list__item"
        v-for="(product, index) in related_products.slice(0, 6)"
        :key="index">
          <a href="#"
          @click="go_page(`front/onlinestore/${$route.params.category}/${product.id}`)">
            <div class="img">
              <img :src="product.images[0]" alt="" class="product">
              <img src="../../assets/icon/new_tag.png" alt=""
              class="isnew" v-if="product.isnew">
            </div>
            <div class="main">
              <div class="title">{{product.title}}</div>
              <div class="code">{{product.id}}</div>
            </div>
          </a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import $ from 'jquery';
import { Splide, SplideSlide } from '@splidejs/vue-splide';
import VueGallerySlideshow from 'vue-gallery-slideshow';

export default {
  props: ['products'],
  // products: [
  //   {
  //     title: '',
  //     id: '',
  //     price: '',
  //     is_new: '',
  //     home_banner: {
  //       on_banner: '',
  //       img: '',
  //     },
  //     category: [],
  //     images: [],
  //     content: '',
  //     description: [
  //       {
  //         title: '',
  //         text: '',
  //       },
  //     ],
  //     user: {
  //       cart_quantity: '',
  //       favorite: '',
  //     },
  //   },
  // ],
  watch: {
    $route() {
      this.initialize_data();
      document.title = `產品介紹 | ${this.current_product.title}`;
    },
  },
  components: {
    Splide,
    SplideSlide,
    VueGallerySlideshow,
  },
  data() {
    return {
      current_product: {},
      related_products: [],
      splideOptions: {
        rewind: true,
        perPage: 1,
        gap: '1rem',
      },
      lightbox_index: null,
      lightbox_showbox: '',
      current_description: '',
    };
  },
  methods: {
    initialize_data() {
      const productId = this.$route.params.id;
      const productCategory = this.$route.params.category;

      const thisProduct = this.products
        .find((product) => product.id === productId);
      let thisCategory;
      if (productCategory === 'all') {
        thisCategory = this.products;
      } else {
        thisCategory = this.products
          .filter((product) => product.category.includes(productCategory));
      }

      this.current_product = JSON.parse(JSON.stringify(thisProduct));
      for (let index = 0; index < this.current_product.description.length; index += 1) {
        if (index === 0) {
          this.current_product.description[index].active = true;
        } else {
          this.current_product.description[index].active = false;
        }
      }
      this.current_description = this.current_product.description[0].text;
      this.related_products = JSON.parse(JSON.stringify(thisCategory));
      const tempStr = this.current_product.images[0];
      this.lightbox_showbox = tempStr;
    },
    go_page(pageString) {
      this.$router.push(`/${pageString}`);
    },
    bind_scroll_category_fixed() {
      const category = document.querySelector('.category');
      const categoryOffsetTop = category.offsetTop;
      $(window).bind('scroll', () => {
        const headerHeight = $('.header').height();
        const windowScrollTop = $(window).scrollTop();
        if (headerHeight + windowScrollTop > categoryOffsetTop) {
          $('#front').addClass('category--fixed');
        } else {
          $('#front').removeClass('category--fixed');
        }
      });
    },
    change_description(index) {
      const trueIndex = this.current_product.description
        .findIndex((description) => description.active === true);
      this.current_product.description[trueIndex].active = false;
      const theDescription = this.current_product.description[index];
      theDescription.active = true;
      this.current_description = theDescription.text;
    },
  },
  created() {
    this.initialize_data();
  },
  mounted() {
    $('#front').addClass('banner--hidden');
    this.bind_scroll_category_fixed();
    document.title = `產品介紹 | ${this.current_product.title}`;
  },
};
</script>
